.c{
    height: 100vh;
    position: relative;
}

.c-bg {
    width: 20px;
    height: 100%;
    background-color: #59b256;
    position: absolute;
}

.c-wrapper {
    padding: 50px;
    display: flex;
}

.c-left{
    flex: 1;
}

.c-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.c-title{
    font-size: 60px;
    width: 50%;
}

.c-info-item{
    display: flex;
    align-items: center;
    margin: 20px 0px;
    font-weight: 500;
    width: 70%;
}

.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
    margin-top: 5px;
}

.c-desc{
    font-weight: 200;
    font-size: 16;
    width: 95%;
}

form{
  margin-top: 20px;
}
input{
    width: 50%;
    height: 30px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

textarea{
    width: 85%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
button{
    width: 30%;
    padding: 15px;
    font-size: 18px;
    border: none;
    background-color: #59b256;
    color: white;
    border-radius: 15px;
    font-weight: 500;
    cursor: pointer;
}



@media screen and (max-width: 480px) {
    .c-wrapper{
        flex-direction: column;
    }

    .c-title{
        font-size: 25px;
    }

}